h1,
p {
  font-family: Lato;
}

.table {
  width: auto;
}

.score-cell {
  white-space: nowrap;
}

.result-cell {
  background: #ddd;
}

.winner-ok {
  background: #5cc55c;
}

.score-ok {
  background: #f9f422;
}

.no-points {
  background: #fb8d8d;
}

.winner {
  margin-bottom: 20px;
}

tbody.fail tr {
  background: #fb8d8d;
}
tbody.two-ok tr:nth-child(1),
tbody.two-ok tr:nth-child(2) {
  background: #5cc5fc;
}
tbody.two-order-ok tr:nth-child(1),
tbody.two-order-ok tr:nth-child(2) {
  background: #5cc55c;
}
tbody.all-ok tr {
  background: #f9f422;
}



